const header = document.querySelector('#sticky-header'),
    line2 = header.querySelector('#sticky-header-line-2'),
    slider = document.querySelector('#main-top-slider');

const animateDuration = 200;
let currentAnimation = Promise.resolve(null);

if (header !== null) {
    function updateSticky() {
        const offsetSticky = slider.clientHeight - 580;
        const shouldBeSticky = window.pageYOffset > offsetSticky;

        currentAnimation = currentAnimation
            .then(shouldBeSticky ? runSetStickyProcess : runSetNormalProcess);
    }

    function runSetStickyProcess(isSticky) {
        return new Promise((resolve, reject) => {
            if (isSticky === true) {
                return resolve(true);
            }

            // hide header
            header.style.opacity = 0;

            // move header to sticky position
            setHeaderSticky(window.pageYOffset);

            // animate fade in
            let start = null;
            requestAnimationFrame(function animate(time) {
                if (start === null) {
                    start = time;
                }
                const progress = time - start;
                header.style.opacity = progress / animateDuration;

                if (progress < animateDuration) {
                    requestAnimationFrame(animate);
                } else {
                    resolve(true);
                }
            });
        });
    }

    function runSetNormalProcess(isSticky) {
        return new Promise((resolve, reject) => {
            if (isSticky === false) {
                return resolve(false);
            }

            // mode header to static position
            setHeaderNormal();

            resolve(false);
        });
    }

    function setHeaderSticky(scrollPosition) {
        header.classList.remove('absolute', 'bg-transparent');
        header.classList.add('fixed', 'bg-primary-base');

        line2.classList.remove('rounded-md', 'xxl:mx-36', 'xl:mx-30', 'lg:mx-12', 'xs:mx-10');
        line2.classList.add('xxl:pl-52', 'xxl:pr-56', 'xl:pl-46', 'xl:pr-50', 'lg:pl-28', 'lg:pr-32', 'xs:pl-26');
    }

    function setHeaderNormal() {
        header.classList.remove('fixed', 'bg-primary-base');
        header.classList.add('absolute', 'bg-transparent');

        line2.classList.add('rounded-md', 'xxl:mx-36', 'xl:mx-30', 'lg:mx-12', 'xs:mx-10');
        line2.classList.remove('xxl:pl-52', 'xxl:pr-56', 'xl:pl-46', 'xl:pr-20', 'lg:pl-28', 'lg:pr-32', 'xs:pl-26');
    }

    window.onscroll = updateSticky;
    document.addEventListener("DOMContentLoaded", updateSticky);
}