import { Swiper, Pagination, Autoplay } from 'swiper/js/swiper.esm.js';
import 'swiper/css/swiper.css';

const autoplayTimeout = 10000;
let animations = [];
let animationDelayIds = [];

Swiper.use([Pagination, Autoplay]);
new Swiper('#main-top-slider', {
    loop: true,
    speed: 800,
    slidesPerView: 1,
    autoplay: {
        delay: autoplayTimeout,
        disableOnInteraction: false,
    },
    pagination: {
        el: '#main-top-slider-nav',
        type: 'bullets',
        bulletClass: '-bullet',
        bulletActiveClass: '!opacity-100',
        renderBullet(index, className) {
            return `
            <div class="focus:outline-none mx-4 flex-1 opacity-50 transition-opacity relative ${className}">
                <canvas class="animated-line absolute top-0 left-0 h-2 w-full"></canvas>
                <div class="h-1 my-px bg-white"></div>
            </div>
            `;
        }
    },
    on: {
        paginationRender() {
            if (this.pagination.bullets === undefined) {
                return;
            }
            
            runNewAnimation(this.realIndex, this.pagination.bullets);
        },
        transitionStart() {
            if (this.pagination.bullets === undefined) {
                return;
            }
            
            clearAnimations(this.pagination.bullets);
        },
        transitionEnd() {
            if (this.pagination.bullets === undefined) {
                return;
            }
            
            runNewAnimation(this.realIndex, this.pagination.bullets);
        }
    }
});

function clearAnimations(navItems) {
    animations.forEach(cancelAnimationFrame);
    animationDelayIds.forEach(clearTimeout);
    animations = [];
    animationDelayIds = [];

    for (var i = 0; i < navItems.length; i++) {        
        const line = navItems[i].querySelector('.animated-line');
        if (line !== null) {
            const context = line.getContext('2d');
            context.clearRect(0, 0, line.width, line.height);
        }
    }
}

function runNewAnimation(displayIndex, navItems) {
    const item = navItems[displayIndex];
    const line = item.querySelector('.animated-line');

    let start = null, animateDuration = autoplayTimeout;
    
    animations[displayIndex] = requestAnimationFrame(function animate(time) {
        if (start === null) {
            start = time;
        }
        const progress = time - start;

        const width = (progress / animateDuration) * line.width;
        const context = line.getContext('2d');
        context.fillStyle = '#00EAFF';
        context.fillRect(0, 0, width, line.height);

        if (progress < animateDuration) {
            animationDelayIds[displayIndex] = setTimeout(function() {
                animations[displayIndex] = requestAnimationFrame(animate);
            }, 50);
        }
    });
}