const searchForms = document.querySelectorAll('[data-search-form]');

searchForms.forEach(node => {
    const searchInput = node.querySelector('[data-search-input]'),
        searchSuggestions = node.querySelector('[data-search-suggestions]');

    searchInput.onfocus = () => searchSuggestions.classList.remove('opacity-0', 'pointer-events-none');
    searchInput.onblur = () => searchSuggestions.classList.add('opacity-0', 'pointer-events-none');
});