// Import Swiper and modules
import { Swiper } from 'swiper/js/swiper.esm.js';
import 'swiper/css/swiper.css';
import screens from '~/tailwind/config/screens';


const sliders = document.querySelectorAll('.events-slider');
for (let i = 0; i < sliders.length; i++) {
    initSlider(sliders[i]);
}

function initSlider(container) {
    new Swiper(container, {
        slidesPerView: 1,
        spaceBetween: 16,
        loop: true,
        breakpoints: {
            [parseInt(screens.xxl)]: {
                slidesPerView: 5
            },
            [parseInt(screens.lg)]: {
                slidesPerView: 4
            },
            [parseInt(screens.md)]: {
                slidesPerView: 3
            },
            [parseInt(screens.xs)]: {
                slidesPerView: 2
            }
        }
    });
}

